import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import { UserProfile } from '@/shared/types/user';

export interface Notification {
  id: string;
  title: string;
  message: string;
  type: 'info' | 'success' | 'warning' | 'error';
  read: boolean;
  created_at: string;
}

interface AppStoreState {
  // User state
  appStoreUser: UserProfile | undefined;
  isAuthenticated: boolean;
  isLoading: boolean;
  
  // Notifications
  notifications: Notification[];
  unreadCount: number;
  
  // Profile & Weight Management
  connectedScales: any[];
  weightHistory: any[];
  
  // App state
  theme: 'light' | 'dark' | 'system';
  sidebarCollapsed: boolean;
  activeFeature: string | undefined;
  
  // Actions
  setUser: (user: UserProfile | null) => void;
  updateUser: (updates: Partial<UserProfile>) => void;
  updateUserProfile: (updates: Partial<UserProfile>) => void;
  setAuthenticated: (isAuth: boolean) => void;
  setLoading: (loading: boolean) => void;
  
  // Profile actions
  addConnectedScale: (scale: any) => void;
  addWeightEntry: (entry: any) => void;
  
  // Notification actions
  addNotification: (notification: Omit<Notification, 'id' | 'created_at'>) => void;
  markNotificationAsRead: (id: string) => void;
  markAllNotificationsAsRead: () => void;
  removeNotification: (id: string) => void;
  clearAllNotifications: () => void;
  
  // Module actions
  activateModule: (moduleId: string) => Promise<boolean>;
  
  // App actions
  setTheme: (theme: 'light' | 'dark' | 'system') => void;
  toggleSidebar: () => void;
  setSidebarCollapsed: (collapsed: boolean) => void;
  setActiveFeature: (feature: string | null) => void;
  
  // Utility actions
  reset: () => void;
  init: () => void;
}

const initialState = {
  appStoreUser: null,
  isAuthenticated: false,
  isLoading: false,
  notifications: [],
  unreadCount: 0,
  connectedScales: [],
  weightHistory: [],
  theme: 'system' as const,
  sidebarCollapsed: false,
  activeFeature: null,
};

export const appStore = create<AppStoreState>()(
  persist(
    (set, get) => ({
      ...initialState,
      
      // User actions
      setUser: (user) => set({ 
        appStoreUser: user,
        isAuthenticated: !!user 
      }),
      
      updateUser: (updates) => set((state) => ({
        appStoreUser: state.appStoreUser 
          ? { ...state.appStoreUser, ...updates }
          : null
      })),
      
      setAuthenticated: (isAuth) => set({ isAuthenticated: isAuth }),
      setLoading: (loading) => set({ isLoading: loading }),
      
      // Profile actions
      updateUserProfile: (updates) => set((state) => ({
        appStoreUser: state.appStoreUser 
          ? { ...state.appStoreUser, ...updates }
          : null
      })),
      
      addConnectedScale: (scale) => set((state) => ({
        connectedScales: [...state.connectedScales, scale]
      })),
      
      addWeightEntry: (entry) => set((state) => ({
        weightHistory: [...state.weightHistory, entry]
      })),
      
      // Notification actions
      addNotification: (notification) => set((state) => {
        const newNotification: Notification = {
          ...notification,
          id: Date.now().toString(),
          created_at: new Date().toISOString(),
        };
        
        return {
          notifications: [newNotification, ...state.notifications],
          unreadCount: state.unreadCount + 1,
        };
      }),
      
      markNotificationAsRead: (id) => set((state) => ({
        notifications: state.notifications.map((notif, index) =>
          notif.id === id ? { ...notif, read: true } : notif
        ),
        unreadCount: Math.max(0, state.unreadCount - 1),
      })),
      
      markAllNotificationsAsRead: () => set((state) => ({
        notifications: state.notifications.map((notif, index) => ({ ...notif, read: true })),
        unreadCount: 0,
      })),
      
      removeNotification: (id) => set((state) => {
        const notification = state.notifications.find(n => n.id === id);
        return {
          notifications: state.notifications.filter(notif => notif.id !== id),
          unreadCount: notification && !notification.read 
            ? Math.max(0, state.unreadCount - 1) 
            : state.unreadCount,
        };
      }),
      
      clearAllNotifications: () => set({ 
        notifications: [], 
        unreadCount: 0 
      }),
      
      // Module actions
      activateModule: async (moduleId: string) => {
        const state = get();
        const currentUser = state.appStoreUser;
        
        if (!currentUser?.id) {
          console.error('No user found for module activation');
          return false;
        }
        
        try {
          // Import supabase dynamically to avoid circular dependencies
          const { supabase } = await import('@/lib/supabase');
          
          // Get current active modules or empty array
          const currentModules = currentUser.active_modules || [];
          
          // Add the new module if not already present
          const updatedModules = currentModules.includes(moduleId) 
            ? currentModules 
            : [...currentModules, moduleId];
          
          // Update user in database
          const { data, error }: any = await supabase
            .from('user_profiles')
            .update({ active_modules: updatedModules })
            .eq('id', currentUser.id)
            .select()
            .single();
          
          if (error) {
            console.error('Error activating module:', error);
            return false;
          }
          
          // Update local store
          set((state) => ({
            appStoreUser: state.appStoreUser 
              ? { ...state.appStoreUser, active_modules: updatedModules }
              : null
          }));
          
          return true;
        } catch (error: any) {
          console.error('Error activating module:', error);
          return false;
        }
      },
      
      // App actions
      setTheme: (theme) => set({ theme }),
      
      toggleSidebar: () => set((state) => ({ 
        sidebarCollapsed: !state.sidebarCollapsed 
      })),
      
      setSidebarCollapsed: (collapsed) => set({ sidebarCollapsed: collapsed }),
      
      setActiveFeature: (feature) => set({ activeFeature: feature }),
      
      // Utility actions
      reset: () => set(initialState),
      
      init: () => {
        // Initialize app state, check auth, etc.
        set((state) => ({
          ...state,
          isLoading: false,
        }));
      },
    }),
    {
      name: 'myfithero-app-store',
      partialize: (state) => ({
        appStoreUser: state.appStoreUser,
        isAuthenticated: state.isAuthenticated,
        theme: state.theme,
        sidebarCollapsed: state.sidebarCollapsed,
        // Don't persist notifications and loading state
      }),
    }
  )
);

// Selectors for better performance
export const useAppStoreUser = () => appStore((state) => state.appStoreUser);
export const useAppStoreAuth = () => appStore((state) => ({
  isAuthenticated: state.isAuthenticated,
  isLoading: state.isLoading,
}));
export const useAppStoreNotifications = () => appStore((state) => ({
  notifications: state.notifications,
  unreadCount: state.unreadCount,
}));
export const useAppStoreUI = () => appStore((state) => ({
  theme: state.theme,
  sidebarCollapsed: state.sidebarCollapsed,
  activeFeature: state.activeFeature,
}));